.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
  text-align: center;
  padding: 0 35px 35px 35px;

  &__content {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  &__title {
    font-family: "Michroma", sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    color: $primary;
    font-size: 42px;
    padding-top: 100px;

    @include tablet {
      font-size: 62px;
    }

    @include desktop {
      font-size: 80px;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;

    @include tablet {
      flex-direction: row;
      justify-content: center;
      gap: 15px;
    }
  }
}

.text {
  &--subheading {
    font-size: 15px;
    font-weight: 400;
    text-transform: uppercase;
        color: $secundary;

    @include tablet {
      font-size: 17px;
    }

    @include desktop {
      font-size: 19px;
    }
  }
}

.btn { 
  padding: 0.9rem 1.2rem;
  width: 100%;
  max-width: 16rem;
  border-radius: 2rem;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  text-decoration: none;

  &--circle {
    width: 50px;
    height: 50px;
    padding: 0;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    font-weight: 600;
    border: 1px solid $hover;
    background: none;
    color: $secundary;
    transition: all 600ms ease-in-out;

    @include tablet {
      width: 55px;
      height: 55px;
      font-size: 15px;
    }
    @include desktop {
      &:hover {
          background: $hover;
          color: white;
          border: none;
      }
    }
  }
}